*,
*::after,
*::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  min-width: 360px;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  font-family: "Raleway", "Arial", sans-serif;
  color: #e6e6e6; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: relative; }

.page-header__wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  width: 1366px;
  margin: 0 auto;
  padding-top: 23px;
  padding-bottom: 32px;
  padding-left: 32px;
  padding-right: 32px; }

.page-header__logo {
  margin-right: auto; }

.page-header__nav {
  margin-bottom: 9px;
  margin-right: 91px; }

.page-header__town {
  position: relative;
  margin-right: 25px;
  margin-bottom: 7px;
  padding: 0;
  padding-top: 6px;
  padding-bottom: 6px;
  font: inherit;
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  letter-spacing: 0.02em;
  color: #f2890f;
  border: none;
  background: none;
  text-transform: uppercase;
  outline: none;
  cursor: pointer; }
  .page-header__town::before {
    content: "";
    position: absolute;
    left: -24px;
    top: 50%;
    transform: translateY(-50%);
    width: 16px;
    height: 21px;
    background-image: url("../img/marker.svg");
    background-repeat: no-repeat; }
  .page-header__town::after {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-image: url("../img/line-dashed-orange.svg");
    background-repeat: repeat-x;
    background-size: cover; }
  .page-header__town:hover::after, .page-header__town:focus::after {
    content: ""; }

.page-header__phone {
  margin-right: 7px;
  margin-bottom: 7px;
  font: inherit;
  font-size: 14px;
  line-height: 16px;
  font-weight: 600;
  letter-spacing: 0.03em;
  text-decoration: none;
  font-variant-numeric: lining-nums;
  font-feature-settings: 'lnum' 1;
  color: #f0f0f0; }
  .page-header__phone:hover, .page-header__phone:focus {
    color: #f2890f; }

.title {
  margin: 0;
  font-weight: 800;
  font-size: 64px;
  line-height: 70px;
  color: #ffffff; }

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.site-list__item {
  margin-right: 48px; }

.site-list__link {
  font: inherit;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.03em;
  color: #f0f0f0; }
  .site-list__link:hover, .site-list__link:focus {
    color: #f2890f; }

.promo {
  position: static;
  width: 1366px;
  margin: 0 auto;
  margin-top: 74px;
  margin-bottom: 125px; }
  .promo::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 1366px;
    height: 768px;
    background-color: #000000;
    background-image: linear-gradient(274.19deg, transparent 35.91%, rgba(36, 21, 14, 0.53) 65.15%, rgba(26, 20, 13, 0.53) 98.48%), url("../../img/bg-index-desktop@1x.jpg");
    background-repeat: no-repeat;
    z-index: -1;
    background-position: top center;
    background-size: 1366px 768px; }

.promo__wrapper {
  padding-left: 136px;
  padding-right: 136px; }

.promo__title {
  width: 530px;
  margin-bottom: 32px; }

.promo__text {
  margin: 0;
  width: 470px;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  margin-bottom: 69px; }

.promo__button {
  display: inline-block;
  padding: 22px 51px;
  font: inherit;
  text-transform: uppercase;
  background-color: #f2890f;
  border-radius: 65px;
  font-weight: 800;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.03em;
  color: #ffffff;
  cursor: pointer; }
  .promo__button:hover, .promo__button:focus {
    background-color: #f39425; }

.page-footer {
  position: sticky;
  bottom: 0;
  width: 1366px;
  padding-left: 32px;
  padding-right: 32px;
  margin: 0 auto;
  padding-bottom: 17px; }

.page-footer__wrapper {
  display: flex;
  justify-content: space-between; }

.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }

.social__item {
  margin-right: 7px;
  height: 28px; }

.social__link {
  position: relative;
  display: block;
  text-decoration: none;
  width: 28px;
  height: 28px; }
  .social__link::before {
    content: "";
    position: absolute;
    width: 28px;
    height: 28px;
    top: 0;
    left: 0;
    background-repeat: no-repeat; }
  .social__link--instagram::before {
    background-image: url("../../img/instagram.svg"); }
  .social__link--vkontakte::before {
    background-image: url("../../img/vk.svg"); }
  .social__link--instagram:hover::before, .social__link--instagram:focus::before {
    background-image: url("../../img/instagram-hover.svg"); }
  .social__link--vkontakte:hover::before, .social__link--vkontakte:focus::before {
    background-image: url("../../img/vk-hover.svg"); }

.page-footer__question {
  position: relative;
  font: inherit;
  font-size: 12px;
  line-height: 17px;
  color: #e6e6e6;
  text-decoration: none;
  margin-bottom: 7px;
  padding-top: 6px;
  padding-bottom: 2px; }
  .page-footer__question:hover, .page-footer__question:focus {
    color: #f2890f; }
  .page-footer__question::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-image: url("../../img/line-dashed-white.svg");
    background-repeat: repeat-x;
    bottom: 0;
    left: 0; }
  .page-footer__question:hover::after, .page-footer__question:focus::after {
    background-image: url("../../img/line-dashed-orange.svg"); }

.popup__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(61, 51, 51, 0.97); }

.popup__wrapper {
  position: fixed;
  width: 480px;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 32px;
  background-image: linear-gradient(0deg, #141414 0%, #1F1D1D 100%);
  border-radius: 3px;
  min-height: 485px; }

.popup__close {
  position: absolute;
  width: 15px;
  height: 15px;
  right: 32px;
  top: 40px;
  border: none;
  background: none;
  opacity: 0.5;
  cursor: pointer; }
  .popup__close::after, .popup__close:before {
    left: -2px;
    content: "";
    position: absolute;
    width: 20px;
    height: 2px;
    background-color: #ffffff;
    transform: rotate(45deg); }
  .popup__close:before {
    transform: rotate(-45deg); }

.popup__title {
  margin: 0;
  font-weight: 800;
  font-size: 32px;
  line-height: 38px;
  color: #ffffff;
  margin-bottom: 28px; }

.popup__location {
  font: inherit;
  position: relative;
  display: inline-block;
  margin-bottom: 25px;
  margin-left: 32px;
  font-weight: 400;
  font-size: 17px;
  line-height: 25px;
  color: #e6e6e6;
  border-bottom: 1px solid #ffffff;
  cursor: pointer; }
  .popup__location::after {
    content: "";
    position: absolute;
    left: -30px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    background-image: url("../img/gprs-location.svg"); }

.popup__text {
  font-size: 17px;
  line-height: 27px; }

.popup__item {
  margin-bottom: 20px; }

.popup__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.popup__link {
  font: inherit;
  font-weight: 800;
  font-size: 24px;
  line-height: 29px;
  color: #ffffff;
  opacity: 0.32;
  text-decoration: none;
  padding-bottom: 2px; }
  .popup__link:hover, .popup__link:focus {
    opacity: 1; }

.popup__item--active .popup__link {
  opacity: 1;
  border-bottom: 2px solid #f2890f; }
